<template>
  <va-modal
    :model-value="modelValue"
    @update:modelValue="$emit('update:modelValue', $event)"
    size="large"
  >
    <template #title>ÏÑ§ÎπÑ Í≤ÄÏÉâ</template>

    <!-- Í≤ÄÏÉâ ÏûÖÎ†• -->
    <div class="va-row va-gap-2 va-mb-4">
      <va-input v-model="searchText" placeholder="ÏÑ§ÎπÑÎ™Ö ÎòêÎäî ID Í≤ÄÏÉâ" />
    </div>

    <!-- ÏÑ§ÎπÑ ÌÖåÏù¥Î∏î -->
    <va-data-table
      :items="filteredEquipments"
      :columns="columns"
      track-by="eq_id"
    >
      <template #cell(action)="{ row }">
        <va-button size="small" @click="selectEquipment(row.rowData)"
          >ÏÑ†ÌÉù</va-button
        >
      </template>
    </va-data-table>
  </va-modal>
</template>

<script setup lang="ts">
import { defineProps, defineEmits, ref, computed } from "vue";

// Props
const props = defineProps<{
  modelValue: boolean;
  equipmentList: Array<any>;
}>();

const emit = defineEmits(["update:modelValue", "apply"]);

// Í≤ÄÏÉâÏñ¥ ÏÉÅÌÉú
const searchText = ref("");

// ÌÖåÏù¥Î∏î ÌïÑÌÑ∞ÎßÅ
const filteredEquipments = computed(() => {
  const keyword = searchText.value.trim().toLowerCase();
  if (!keyword) return props.equipmentList;
  return props.equipmentList.filter(
    (e) =>
      e.eq_name?.toLowerCase().includes(keyword) ||
      e.eq_id?.toLowerCase().includes(keyword),
  );
});

// ÌÖåÏù¥Î∏î Ïª¨Îüº
const columns = [
  { key: "eq_id", label: "ÏÑ§ÎπÑ ID" },
  { key: "eq_name", label: "ÏÑ§ÎπÑÎ™Ö" },
  { key: "code_label", label: "ÏÉÅÌÉú" },
  { key: "action", label: "ÏÑ†ÌÉù", width: 100 },
];

// ÏÑ†ÌÉùÏãú emit
const selectEquipment = (item: any) => {
  emit("apply", item);
  emit("update:modelValue", false); // ÌåùÏóÖ Îã´Í∏∞
};
</script>
