<template>
  <div class="va-pa-4">
    <!-- Í≤ÄÏÉâ ÌïÑÌÑ∞ -->
    <div class="search-grid">
      <va-input
        v-model="filters.workOrder"
        label="ÏûëÏóÖÏßÄÏãúÏÑú Î≤àÌò∏"
        placeholder="ÏûëÏóÖÏßÄÏãúÏÑú Î≤àÌò∏ ÏûÖÎ†•"
        readonly
      />
      <va-input
        v-model="filters.productName"
        label="Ï†úÌíàÎ™Ö"
        placeholder="Ï†úÌíàÎ™Ö ÏûÖÎ†•"
        readonly
      />
      <va-input
        v-model="filters.productSpec"
        label="Ï†úÌíàÍ∑úÍ≤©"
        placeholder="Ï†úÌíà Í∑úÍ≤© ÏûÖÎ†•"
        readonly
      />
      <va-input
        v-model="filters.resultId"
        label="ÏûëÏóÖÏã§Ï†Å ID"
        placeholder="ÏûëÏóÖ Ïã§Ï†Å ID ÏûÖÎ†•"
        readonly
      />
      <va-date-input
        v-model="filters.workDate"
        label="ÏûëÏóÖÏùºÏûê"
        :manual-input="false"
        :clearable="true"
      />

      <div class="button-group">
        <va-button color="info" @click="openProductPopup"
          >ÏûëÏóÖÏßÄÏãú Í≤ÄÏÉâ</va-button
        >
        <va-button color="primary" @click="searchItems">Ï°∞Ìöå</va-button>
        <va-button color="secondary" @click="resetFilters">Ï¥àÍ∏∞Ìôî</va-button>
      </div>
    </div>

    <!-- Î¶¨Ïä§Ìä∏ ÌÖåÏù¥Î∏î -->
    <va-data-table :items="workList" :columns="columns" track-by="result_id">
      <template #cell(select)="{ row }">
        <va-checkbox
          :model-value="row.rowData.selected"
          @update:modelValue="(val) => handleCheck(row.rowData, val)"
        />
      </template>
      <template #cell(work_start_time)="{ row }">
        {{ formatTime(row.rowData.work_start_time) }}
      </template>
      <template #cell(work_end_time)="{ row }">
        {{ formatTime(row.rowData.work_end_time) }}
      </template>
      <template #cell(duration)="{ row }">
        {{
          calculateDuration(
            row.rowData.work_start_time,
            row.rowData.work_end_time,
          )
        }}
      </template>
    </va-data-table>

    <!-- ÌÉ≠ Î≤ÑÌäº -->
    <div class="va-mt-6 tab-toggle">
      <va-button-toggle
        v-model="activeTab"
        :options="[
          { label: 'ÏûëÏóÖÏã§Ï†Å ÏûÖÎ†•', value: 'input' },
          { label: 'ÏûêÏû¨ ÎÇ¥Ïó≠', value: 'material' },
        ]"
        color="primary"
        toggle-color="primary"
      />
    </div>

    <!-- v-model.number="selectedItem.pass_qty"-->
    <!-- ÏûëÏóÖÏã§Ï†Å ÏûÖÎ†• -->
    <div v-if="activeTab === 'input'" class="input-layout">
      <div class="input-grid">
        <va-input
          v-model="selectedItem.process_code"
          label="Í≥µÏ†ïÏΩîÎìú"
          readonly
        />
        <va-input v-model="selectedItem.manager_id" label="ÏûëÏóÖÏûêÎ™Ö" />
        <va-input v-model="selectedItem.product_name" label="Ï†úÌíàÎ™Ö" readonly />
        <va-input
          v-model="selectedItem.product_stand"
          label="Ï†úÌíàÍ∑úÍ≤©"
          readonly
        />
        <div class="flex items-center gap-1">
          <va-input
            v-model="selectedItem.eq_name"
            label="ÏÑ§ÎπÑÎ™Ö"
            readonly
            class="flex-1"
          />
          <va-button
            icon="search"
            color="primary"
            @click="openEquipmentPopup(selectedItem.eq_type_code)"
            class="h-[38px] mt-[20px]"
          >
            <svg
              width="16"
              height="16"
              viewBox="0 0 24 24"
              fill="none"
              stroke="currentColor"
              stroke-width="2"
              stroke-linecap="round"
              stroke-linejoin="round"
            >
              <circle cx="11" cy="11" r="8" />
              <path d="m21 21-4.35-4.35" />
            </svg>
          </va-button>
        </div>
        <va-input v-model="selectedItem.code_label" label="Í∞ÄÎèôÏÉÅÌÉú" readonly />
        <va-input
          :model-value="formatTime(selectedItem.work_start_time)"
          label="ÏãúÏûëÏãúÍ∞Ñ"
          readonly
        />
        <va-input
          :model-value="formatTime(selectedItem.work_end_time)"
          label="Ï¢ÖÎ£åÏãúÍ∞Ñ"
          readonly
        />
        <va-input v-model.number="selectedItem.pass_qty" label="ÏÉùÏÇ∞ÏàòÎüâ" />
        <va-select
          v-model="selectedItem.end_reason_code"
          :options="endReasonOptions"
          track-by="value"
          value-by="value"
          text-by="label"
          label="ÏûëÏóÖ Ï¢ÖÎ£å ÏÇ¨Ïú†"
          placeholder="Ï¢ÖÎ£å ÏÇ¨Ïú† ÏÑ†ÌÉù"
        />
      </div>
      <div class="side-buttons">
        <!-- ÏûëÏóÖÏãúÏûë -->
        <va-button
          color="primary"
          @click="isPackagingProcess ? notifyPackagingAlert() : startWork()"
        >
          ÏûëÏóÖÏãúÏûë
        </va-button>

        <!-- ÏûëÏóÖÏ¢ÖÎ£å -->
        <va-button
          color="danger"
          @click="isPackagingProcess ? notifyPackagingAlert() : endWork()"
        >
          ÏûëÏóÖÏ¢ÖÎ£å
        </va-button>
      </div>
    </div>

    <!-- ÏûêÏû¨ ÎÇ¥Ïó≠ -->
    <div v-else-if="activeTab === 'material'">
      <div v-if="materialMessage" class="va-text-danger va-mb-2">
        {{ materialMessage }}
      </div>

      <va-data-table
        v-else
        :items="materialList"
        :columns="[
          { key: 'material_code', label: 'ÏûêÏû¨ÏΩîÎìú' },
          { key: 'material_name', label: 'ÏûêÏû¨Î™Ö' },
          { key: 'outbound_qty', label: 'Ï∂úÍ≥†Îüâ' },
        ]"
        track-by="material_code"
      />
      <div class="va-mb-3">
        <va-button color="primary" @click="fetchMaterialList"
          >üîç ÏûêÏû¨ ÎÇ¥Ïó≠ Í≤ÄÏÉâ</va-button
        >
      </div>
    </div>

    <!-- üîç Ï†úÌíà Í≤ÄÏÉâ ÌåùÏóÖ -->
    <ProductSearchModal
      v-model:visible="isProductPopupOpen"
      @apply="applyProduct"
    />

    <!-- üîç ÏÑ§ÎπÑ Í≤ÄÏÉâ ÌåùÏóÖ -->
    <EquipmentSearchModal
      v-model="isEquipmentPopupOpen"
      :equipmentList="equipmentList"
      @apply="applyEquipment"
    />
  </div>
</template>

<script setup lang="ts">
import { ref, computed, onMounted } from "vue";
import axios from "axios";
import ProductSearchModal from "../modals/ProductSearchModal.vue";
import EquipmentSearchModal from "../modals/EquipmentSearchModal.vue";

interface WorkItem {
  result_id: string;
  result_detail: number;
  product_name: string;
  product_stand: string;
  process_code: string;
  process_name?: string;
  work_start_date: string;
  work_start_time: string;
  work_end_time: string | null;
  work_order_qty: number;
  pass_qty: number | null;
  result_code_label: string;
  detail_code_label: string;
  manager_id: string;
  product_qual_qty: string;
  eq_id: string;
  eq_name: string;
  code_label: string;
  selected?: boolean;
  result_remark?: string;
  eq_type_code: string;
  end_reason_code?: string | { value: string; label: string };
  process_defective_qty: string;
}

const emptyItem = (): WorkItem => ({
  result_id: "",
  result_detail: 0,
  product_name: "",
  product_stand: "",
  process_code: "",
  process_name: "",
  eq_id: "",
  eq_name: "",
  code_label: "",
  work_start_date: "",
  work_start_time: "",
  work_end_time: "",
  work_order_qty: 0,
  pass_qty: 0,
  product_qual_qty: "",
  result_code_label: "",
  detail_code_label: "",
  manager_id: "",
  eq_type_code: "",
  selected: false,
  result_remark: "",
  end_reason_code: "",
  process_defective_qty: "",
});

const allResultList = ref<WorkItem[]>([]);
const workList = ref<WorkItem[]>([]);
const selectedItem = ref<WorkItem>(emptyItem());
const activeTab = ref<"input" | "material">("input");
const materialList = ref<any[]>([]); // ÏûêÏû¨ Îç∞Ïù¥ÌÑ∞
const materialMessage = ref(""); // ÏïàÎÇ¥ Î©îÏãúÏßÄ

const columns = [
  { key: "select", label: "ÏÑ†ÌÉù", width: 40 },
  { key: "result_id", label: "Ïã§Ï†ÅID" },
  { key: "product_name", label: "Ï†úÌíàÎ™Ö" },
  { key: "product_stand", label: "Ï†úÌíàÍ∑úÍ≤©" },
  { key: "process_code", label: "Í≥µÏ†ïÏΩîÎìú" },
  { key: "process_name", label: "Í≥µÏ†ïÎ™Ö" },
  { key: "work_start_time", label: "ÏãúÏûëÏãúÍ∞Ñ" },
  { key: "work_end_time", label: "Ï¢ÖÎ£åÏãúÍ∞Ñ" },
  { key: "process_time", label: "ÏòàÏÉÅÏãúÍ∞Ñ(Î∂Ñ)" },
  { key: "duration", label: "ÏÜåÏöîÏãúÍ∞Ñ(Î∂Ñ)" },
  { key: "work_order_qty", label: "ÏöîÏ≤≠ÏàòÎüâ" },
  { key: "pass_qty", label: "Ïû¨ÏûëÏàòÎüâ" },
  { key: "detail_code_label", label: "ÏßÑÌñâÏÉÅÌÉú" },
  { key: "manager_id", label: "ÏûëÏóÖÏûê" },
];

const filters = ref({
  workOrder: "",
  workDate: "",
  resultId: "",
  productName: "",
  productSpec: "",
  worker: "",
});

type EndReasonRaw = {
  code_label?: string;
  code_value?: string;
  label?: string;
  value?: string;
};

const endReasonList = ref<{ code_value: string; code_label: string }[]>([]);

const fetchEndReasons = async () => {
  try {
    const res = await axios.get("/endEq");
    const rawList: EndReasonRaw[] = res.data;
    endReasonList.value = rawList.map((item) => ({
      code_label: item.code_label ?? item.label ?? "",
      code_value: item.code_value ?? item.value ?? "",
    }));
    console.log("‚úÖ endReasonList:", endReasonList.value);
    console.log("‚úÖ endReasonOptions:", endReasonOptions.value); // üí° Ïù¥ ÏãúÏ†êÏóêÎäî Í∞í ÏûàÏùå
  } catch (err) {
    console.error("ÏûëÏóÖÏ¢ÖÎ£å ÏÇ¨Ïú† ÏΩîÎìú Î∂àÎü¨Ïò§Í∏∞ Ïã§Ìå®", err);
  }
};

console.log("‚úÖ endReasonList:", endReasonList.value);

const endReasonOptions = computed(() =>
  endReasonList.value.map((item) => ({
    value: String(item.code_value ?? ""),
    label: String(item.code_label ?? ""),
  })),
);

const fetchMaterialList = async () => {
  if (!selectedItem.value.result_id || !selectedItem.value.process_code) {
    materialMessage.value = "‚ö†Ô∏è Í≥µÏ†ïÏùÑ ÏÑ†ÌÉùÌï¥Ï£ºÏÑ∏Ïöî.";
    materialList.value = [];
    return;
  }

  try {
    const res = await axios.get(
      `/materialOutbound/${selectedItem.value.process_code}`,
    );
    if (res.data.length === 0) {
      materialMessage.value = "üîç Ï∂úÍ≥†Îêú ÏûêÏû¨ ÎÇ¥Ïó≠Ïù¥ ÏóÜÏäµÎãàÎã§.";
      materialList.value = [];
    } else {
      materialList.value = res.data;
      materialMessage.value = "";
    }
  } catch (err) {
    materialMessage.value = "‚ùå ÏûêÏû¨ ÎÇ¥Ïó≠ Ï°∞Ìöå Ïã§Ìå®";
    materialList.value = [];
    console.error(err);
  }
};

const isPackagingProcess = computed(() => {
  return selectedItem.value.process_name === "Ìè¨Ïû•";
});

const notifyPackagingAlert = () => {
  alert(
    "üì¶ Ìè¨Ïû• Í≥µÏ†ïÏùÄ Ìè¨Ïû• ÌåÄÏóêÏÑú Ï≤òÎ¶¨Ìï©ÎãàÎã§.\nÏûëÏóÖÏùÑ ÏãúÏûë/Ï¢ÖÎ£åÌï† Ïàò ÏóÜÏäµÎãàÎã§.",
  );
};

const startWork = async () => {
  const eqStatus = selectedItem.value.code_label; // ÏÑ§ÎπÑ ÏÉÅÌÉú

  const selectedSeq =
    Number(selectedItem.value.process_code.match(/\d+$/)?.[0]) || 0;

  // ‚úÖ Ïù¥Ï†Ñ Í≥µÏ†ï Ï§ë ÏôÑÎ£åÎêòÏßÄ ÏïäÏùÄ Í≥µÏ†ïÏù¥ ÏûàÎã§Î©¥ ÏûëÏóÖ ÏãúÏûë Î∂àÍ∞Ä
  const unfinishedPrev = workList.value.find((item) => {
    const currentSeq = Number(item.process_code.match(/\d+$/)?.[0]) || 0;
    return (
      item.result_id === selectedItem.value.result_id &&
      currentSeq < selectedSeq &&
      item.detail_code_label !== "ÏôÑÎ£å" // Ïù¥Ï†Ñ Í≥µÏ†ïÏù¥ ÏôÑÎ£å ÏÉÅÌÉúÍ∞Ä ÏïÑÎãò
    );
  });

  if (unfinishedPrev) {
    alert(
      `‚ö†Ô∏è Ïù¥Ï†Ñ Í≥µÏ†ï "${unfinishedPrev.process_name}"Ïùò ÏßÑÌñâÏÉÅÌÉúÍ∞Ä '${unfinishedPrev.detail_code_label}'ÏûÖÎãàÎã§.\nÏôÑÎ£å ÌõÑ Îã§Ïùå Í≥µÏ†ïÏùÑ ÏãúÏûëÌï¥Ï£ºÏÑ∏Ïöî.`,
    );
    return;
  }

  // ‚úÖ ÏÑ§ÎπÑ ÏÉÅÌÉú Í≤ÄÏÇ¨
  if (eqStatus === "Í∞ÄÎèô Ï§ë") {
    alert(
      "‚ö†Ô∏è ÌòÑÏû¨ ÏÑ§ÎπÑÎäî Í∞ÄÎèôÏ§ëÏûÖÎãàÎã§.\nÍ∞ÄÎèôÏù¥ Ï¢ÖÎ£åÎêòÍ≥† Ï†êÍ≤Ä Î∞è Ï≤≠ÏÜåÍ∞Ä ÏôÑÎ£åÎêú ÌõÑ ÏÇ¨Ïö©ÌïòÏã≠ÏãúÏò§.",
    );
    return;
  }

  if (eqStatus === "Ï†ïÏßÄ") {
    alert(
      "‚ö†Ô∏è ÌòÑÏû¨ ÏÑ§ÎπÑÎäî Ï†ïÏßÄ ÏÉÅÌÉúÏûÖÎãàÎã§.\nÏ†êÍ≤Ä Î∞è Ï≤≠ÏÜåÍ∞Ä Î™®Îëê ÏôÑÎ£åÎêòÍ∏∞Î•º Í∏∞Îã§Î†§Ï£ºÏÑ∏Ïöî.",
    );
    return;
  }

  // ‚úÖ ÏûëÏóÖ ÏãúÏûë Ï≤òÎ¶¨
  const now = new Date();
  selectedItem.value.work_start_time = now.toISOString();
  selectedItem.value.work_start_date = now.toISOString().split("T")[0];

  try {
    if (selectedSeq === 1) {
      await axios.put(`/workResultStatus/${selectedItem.value.result_id}`);
    }

    await axios.put(`/prodResult/${selectedItem.value.result_detail}`, {
      pass_qty: selectedItem.value.pass_qty ?? 0,
      manager_id: selectedItem.value.manager_id ?? "",
      eq_id: selectedItem.value.eq_id ?? "",
    });

    await axios.put(`/eqStatus/${selectedItem.value.eq_id}`);

    alert("‚úÖ ÏûëÏóÖÏù¥ ÏãúÏûëÎêòÏóàÏäµÎãàÎã§.");
    fetchResultList();
  } catch (err) {
    alert("‚ùå ÏÑ§ÎπÑ ÏÉÅÌÉú ÏóÖÎç∞Ïù¥Ìä∏Ïóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.");
    console.error(err);
  }
};

const endWork = async () => {
  if (!selectedItem.value.result_detail) {
    alert("‚ö†Ô∏è ÏûëÏóÖ Ïã§Ï†ÅÏù¥ ÏÑ†ÌÉùÎêòÏßÄ ÏïäÏïòÏäµÎãàÎã§.");
    return;
  }

  const reasonCode =
    typeof selectedItem.value.end_reason_code === "string"
      ? selectedItem.value.end_reason_code
      : ((selectedItem.value.end_reason_code as { value: string })?.value ??
        "");

  if (reasonCode.trim() === "") {
    alert("‚ö†Ô∏è Ï¢ÖÎ£å ÏÇ¨Ïú†(ÎπÑÍ≥†)Î•º ÏûëÏÑ±Ìï¥Ï£ºÏÑ∏Ïöî.");
    return;
  }

  const now = new Date();
  const endTime = now.toISOString();
  selectedItem.value.work_end_time = endTime;

  try {
    // ‚úÖ Ïã§Ï†Å ÏÉÅÏÑ∏ ÏóÖÎç∞Ïù¥Ìä∏
    await axios.put(`/prodResultStop/${selectedItem.value.result_detail}`, {
      pass_qty: selectedItem.value.pass_qty,
      result_remark: selectedItem.value.end_reason_code,
    });

    // ‚úÖ ÏÑ§ÎπÑ ÏÉÅÌÉú ÏóÖÎç∞Ïù¥Ìä∏
    await axios.put(`/eqStop/${selectedItem.value.eq_id}`, {
      stop_reason: selectedItem.value.end_reason_code,
    });
    alert("‚úÖ ÏûëÏóÖÏù¥ Ï¢ÖÎ£åÎêòÏóàÏäµÎãàÎã§.");
    selectedItem.value = emptyItem();
    fetchResultList();
  } catch (err) {
    console.error(err);
    alert("‚ùå ÏûëÏóÖ Ï¢ÖÎ£å Ï≤òÎ¶¨ Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.");
  }
};

const formatTime = (value: string | null): string => {
  if (!value) return "-";
  const date = new Date(value);
  return date.toLocaleTimeString("ko-KR", {
    hour: "2-digit",
    minute: "2-digit",
  });
};

const calculateDuration = (start: string, end: string | null): string => {
  if (!start || !end) return "-";
  const startTime = new Date(start);
  const endTime = new Date(end);
  const diffMs = endTime.getTime() - startTime.getTime();
  const diffMin = Math.floor(diffMs / 60000);
  return `${diffMin}Î∂Ñ`;
};

const handleCheck = (item: WorkItem, val: boolean) => {
  workList.value.forEach((i) => (i.selected = false));
  item.selected = val;
  if (val) selectedItem.value = { ...item };
};

const fetchResultList = async () => {
  try {
    if (!filters.value.workOrder) {
      alert("ÏûëÏóÖÏßÄÏãúÏÑú Î≤àÌò∏Î•º ÏûÖÎ†•ÌïòÏÑ∏Ïöî.");
      return;
    }

    const res = await axios.get(
      `/prodResult/${filters.value.workOrder}/${filters.value.productSpec}`,
    );
    const mapped: WorkItem[] = res.data.map((item: any) => ({
      ...item,
      code_label: item.cq_code_label,             // ÏÑ§ÎπÑ ÏÉÅÌÉú
      detail_code_label: item.cd_code_label,      // Í≥µÏ†ï ÏÉÅÌÉú
      result_code_label: item.cm_code_label,
      selected: false,
    }));

    if (mapped.length === 0) {
      alert("ÏûêÏû¨Î•º Ï∂úÍ≥†ÌïòÏÑ∏Ïöî.!");
      selectedItem.value = Object.assign(emptyItem(), {
        product_name: filters.value.productName,
        product_stand: filters.value.productSpec,
        result_id: filters.value.resultId,
        manager_id: filters.value.worker,
      });
      return;
    }

    // üî• Ïù¥ Î∂ÄÎ∂ÑÏù¥ Îπ†Ï†∏ ÏûàÏúºÎ©¥ ÌôîÎ©¥Ïóê Ïïà ÎÇòÏò¥
    allResultList.value = mapped;
    workList.value = mapped; // ‚úÖ Ïù¥Í≤å Î∞òÎìúÏãú ÏûàÏñ¥Ïïº Ìï®
  } catch (err) {
    alert("Ï°∞Ìöå Ïã§Ìå®: Ï°¥Ïû¨ÌïòÏßÄ ÏïäÎäî ÏûëÏóÖÏßÄÏãúÏÑúÏùº Ïàò ÏûàÏäµÎãàÎã§.");
  }
};

const searchItems = async () => {
  await fetchResultList();
};

const resetFilters = () => {
  filters.value = {
    workOrder: "",
    workDate: "",
    resultId: "",
    productName: "",
    productSpec: "",
    worker: "",
  };
  workList.value = [];
  allResultList.value = [];
};

// üîç Ï†úÌíà Í≤ÄÏÉâ ÌåùÏóÖ
const isProductPopupOpen = ref(false);
const openProductPopup = () => {
  isProductPopupOpen.value = true;
};
const applyProduct = (product: any) => {
  filters.value.productName = product.product_name;
  filters.value.productSpec = product.product_stand;
  filters.value.workOrder = product.work_order_no;
  filters.value.resultId = product.result_id;
  selectedItem.value.result_id = product.result_id;
  isProductPopupOpen.value = false;
};

const isEquipmentPopupOpen = ref(false);
const equipmentList = ref<any[]>([]); // ÏûêÏãùÏóêÍ≤å Ï§Ñ Îç∞Ïù¥ÌÑ∞
const openEquipmentPopup = async (eqTypeCode: string) => {
  try {
    console.log(eqTypeCode);
    const res = await axios.get(`/equipment/${eqTypeCode}`);
    equipmentList.value = res.data; // Î∞õÏïÑÏò® Îç∞Ïù¥ÌÑ∞ Ï†ÄÏû•
    console.log(res.data);
    isEquipmentPopupOpen.value = true;
  } catch (err) {
    alert("ÏÑ§ÎπÑ Îç∞Ïù¥ÌÑ∞Î•º Î∂àÎü¨Ïò§ÏßÄ Î™ªÌñàÏäµÎãàÎã§.");
  }
};

const applyEquipment = (equipment: any) => {
  selectedItem.value.eq_id = equipment.eq_id;
  selectedItem.value.eq_name = equipment.eq_name;
  selectedItem.value.code_label = equipment.code_label;
  isEquipmentPopupOpen.value = false;
};

onMounted(() => {
  fetchEndReasons();
});
</script>

